.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #ff6b01;
  border-radius: 50%;
  transition: transform linear;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

