// @charset 'utf-8';

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  /* height: 28px;
     margin: 14px 0 12px; */
  display: flex;
  justify-content: center;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  /* display: inline-block;
     position: relative;
     width: 28px;
     padding: 8px 0 6px;
     margin-left: 0;
     color: #333;
     font-size: 12px;
     font-weight: normal;
     line-height: 1;
     text-decoration: none;
     vertical-align: middle;
     border: 1px solid #ddd;
     border-width: 1px 0; */
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 0em;
  text-decoration: none;
  color: var(--main-title-color);
  background-color: transparent;
  border: none;
  border-radius: 4px;
}

.tui-pagination a.tui-page-btn:hover {
  background-color: #ff6b087e;
}
a.tui-page-btn.tui-prev-is-ellip.tui-first-child:hover {
  background-color: #ff6b087e;
}
a.tui-page-btn.tui-next-is-ellip.tui-last-child:hover {
  background-color: #ff6b087e;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #ff6b08;
  border-color: #ff6b08;
  cursor: default;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-prev-is-ellip {
  border-left: 1px solid #ddd;
}

.tui-pagination .tui-next-is-ellip {
  border-right: 1px solid #ddd;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 26px;
  padding: 0;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  border: 1px solid #ddd;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-left: 10px;
  margin-right: 10px;
}

.tui-pagination .tui-next {
  margin-left: 10px;
  margin-right: 10px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #4b96e6;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

/*   */

a.tui-page-btn {
  border: none;
}

a.tui-page-btn.tui-prev,
a.tui-page-btn.tui-next,
span.tui-page-btn.tui-is-disabled.tui-prev,
span.tui-page-btn.tui-is-disabled.tui-next,
a.tui-page-btn.tui-first,
a.tui-page-btn.tui-last,
span.tui-page-btn.tui-is-disabled.tui-first,
span.tui-page-btn.tui-is-disabled.tui-last,
a.tui-page-btn.tui-prev-is-ellip.tui-first-child,
a.tui-page-btn.tui-next-is-ellip.tui-last-child {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: none;
  border-radius: 4px;
}

@media screen and (max-width: 767px) {
  a.tui-page-btn.tui-prev-is-ellip.tui-first-child,
  a.tui-page-btn.tui-next-is-ellip.tui-last-child,
  a.tui-page-btn.tui-first,
  a.tui-page-btn.tui-last,
  span.tui-page-btn.tui-is-disabled.tui-first,
  span.tui-page-btn.tui-is-disabled.tui-last {
    display: none;
  }
}

.tui-pagination.hidden {
  display: none;
}
.wraper {
  margin-bottom: 20px;
}
